.about{
  padding-top: 85px;
  background-color: #000000e0;
  padding-bottom: 90px;
  border-bottom: 1px solid #787373;

  @include big-desktop{
    padding-top: 80px;
    padding-bottom: 70px;
  }

  @include tablet{
    padding-top: 65px;
    padding-bottom: 60px;
  }

  @include small-tablet{
    padding-top: 50px;
    padding-bottom: 50px;
  }

  @include small-duo{
    padding-top: 45px;
    padding-bottom: 40px;
  }

  @include mobile{
    padding-top: 40px;
    padding-bottom: 30px;
  }

  @include mobile-min{
    padding-top: 30px;
    padding-bottom: 20px;
  }


  &__title{
    @include flex-all-sb;

    &-top{
      font-weight: 700;
      font-size: 45px;
      color: #FFFFFFCC;

      @include big-desktop{
        font-weight: 500;
      }

      @include tablet{
        font-weight: 400;
      }

      @include small-tablet{
        font-size: 30px;
      }

      @include small-duo{
        font-size: 27px;
      }

      @include mobile{
        font-size: 23px;
      }

      @include mobile-min{
        font-size: 20px;
      }

    }
  }

  &__info{
    width: 800px;
  }

  &__text{
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: #FFFFFFCC;


  @include small-duo{
    font-size: 13px;
  }

  @include mobile{
    line-height: 20px;
    font-size: 12px;
  }

  @include mobile-min{
    font-size: 8px;
  }

  }

  &__title-bottom{
    font-weight: 700;
    font-size: 35px;
    color: #FFFFFFCC;

    @include big-desktop{
      font-weight: 500;
    }

    @include tablet{
      font-weight: 400;
    }

    @include small-tablet{
      font-size: 30px;
    }

    @include small-duo{
      font-size: 27px;
    }

    @include mobile{
      font-size: 23px;
    }

    @include mobile-min{
      font-size: 20px;
    }

  }

}

iframe{
  @include big-desktop{
    width: 800px;
    height: 450px;
  }

  @include tablet{
    width: 750px;
    height: 400px;
  }

  @include tablet-pro{
    width: 890px;
  }

  @include tablet-ipad{
    width: 785px;
    height: 400px;
  }

  @include small-tablet{
    width: 750px;
  }

  @include small-duo{
    width: 510px;
    height: 350px;
  }

  @include mobile{
    width: 400px;
    height: 300px;
  }

  @include mobile-pro{
    width: 360px;
  }

  @include mobile-min{
    width: 350px;
    height: 250px;
  }

  @include mobile-small{
    width: 300px;
  }
}
