.programs {
  position: relative;
  top: 0px;
  left: 0;
  padding-top: 50px;
  background-image: url("../img/phone.jpg");
  background-size: cover;
  width: 100%;
  height: 100%;

  @include tablet {
    padding-top: 50px;
    height: 400px;
  }

  @include small-tablet {
    padding-top: 40px;
    height: 350px;
  }

  @include small-duo {
    padding-top: 30px;
    height: 265px;
  }

  @include mobile {
    padding-top: 30px;
    height: 250px;
  }

  @include mobile-min {
    height: 230px;
    padding-top: 15px;
  }

  @include mobile-small {
    padding-top: 10px;
    height: 220px;
  }

  &__item {
    display: flex;
    justify-content: center;

    @include mobile-min {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column-reverse
    }

  }

  &__title {
    max-width: 600px;

    @include tablet {
      max-width: 450px;
    }

    @include small-tablet {
      max-width: 320px;
    }

    @include small-duo {
      max-width: 300px;
    }

    @include mobile {
      max-width: 220px;
    }

    @include mobile-min {
      max-width: 375px;
      display: flex;
      align-items: center;
      flex-direction: column;
    }

    @include mobile-small {
      max-width: 320px;
    }
  }

  &__events {
    font-weight: 500;
    font-size: 50px;
    color: white;

    @include tablet {
      font-size: 40px;
    }

    @include small-tablet {
      font-size: 30px;
    }

    @include small-duo {
      font-size: 25px;
    }

    @include mobile {
      font-size: 20px;
    }

    @include mobile-min {
      font-weight: 400;
      font-size: 15px;
    }
  }

  &__text {
    padding-bottom: 45px;
    font-weight: 400;
    font-size: 20px;
    color: white;

    @include tablet {
      padding-bottom: 30px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include small-duo {
      font-size: 13px;
    }

    @include mobile {
      font-size: 10px;
    }

    @include mobile-min {
      padding-bottom: 8px;
    }

    @include mobile-small {
      padding-left: 15px;
    }
  }

  &__image {
    display: block;
    border-radius: 10px;
    width: 100%;
    height: 400px;
    object-fit: cover;

    @include tablet {
      width: 450px;
      height: 300px;
    }

    @include small-tablet {
      width: 370px;
      height: 275px;
    }

    @include small-duo {
      width: 290px;
      height: 210px;
    }

    @include mobile {
      width: 280px;
      height: 175px
    }

    @include mobile-min {
      border-radius: 5px;
      width: 376px;
      height: 100px
    }

    @include mobile-small {
      width: 320px;
      height: 115px
    }
  }

  &-swiper__next {
    opacity: 0.5;
    background-color: rgba(69, 67, 67, 0.3);
    color: white;
  }

  &-swiper__prev {
    opacity: 0.5;
    background-color: rgba(69, 67, 67, 0.3);
    color: white;
  }
}
