.date {
  padding-top: 10px;
  padding-bottom: 30px;
  background-color: #000000e0;

  @include big-desktop {
    padding-top: 1px;
  }

  @include mobile {
    padding-bottom: 15px;
  }

  @include mobile-min {
    padding-bottom: 10px;
  }
}


.tabs {
  &__title {
    display: flex;
    justify-content: flex-start;
  }

  &__text {
    font-weight: 500;
    font-size: 45px;
    color: white;

    @include big-desktop {
      font-weight: 500;
      padding-left: 35px;
    }

    @include tablet {
      font-weight: 400;
      padding-left: 55px;
    }

    @include small-tablet {
      font-size: 30px;
      padding-left: 60px;
    }

    @include small-duo {
      font-size: 27px;
    }

    @include mobile {
      font-size: 23px;
      padding-left: 0px;
    }

    @include mobile-min {
      font-size: 20px;
    }
  }

  &__nav {
    padding-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 40%;

    @include big-desktop {
      padding-left: 35px;
    }

    @include tablet {
      padding-bottom: 30px;
      padding-left: 52px;
      max-width: 55%;
    }

    @include small-tablet {
      padding-left: 60px;
    }

    @include mobile {
      padding-left: 0px;
    }

    @include mobile-min {
      padding-bottom: 15px;
    }

    &-btn {
      font-weight: 500;
      font-size: 20px;
      color: white;

      @include small-tablet {
        font-size: 14px;
      }

      @include small-duo {
        font-size: 11px;
      }

      @include mobile {
        font-size: 10px;
      }


      &:focus {
        color: #696161;
        background-color: transparent;
      }

      &--active {
        outline: none;
        color: #696161;
        background-color: transparent;
      }
    }
  }




  .popup {
    &__top {
      display: grid;
      grid-template-columns: repeat(20, 1fr);
      gap: 42px;

      @include tablet {
        gap: 40px;
      }

      @include small-tablet {
        gap: 23px;
      }

      @include small-duo {
        gap: 22px;
      }

      @include mobile-pro {
        gap: 21px;
      }

      @include mobile-min {
        gap: 20px;
      }

      @include mobile-small {
        gap: 17px;
      }
    }

    &__cover {
      grid-column: 4 span;

      @include tablet-pro {
        grid-column: 5 span;
      }

      @include tablet-ipad {
        grid-column: 5 span;
      }

      @include small-tablet {
        grid-column: 5 span;
      }

      @include small-duo {
        grid-column: 5 span;
      }

      @include mobile {
        grid-column: 6 span;
      }
    }

    &__link {
      &:hover {
        box-shadow: 0 10px 26px rgba(0, 0, 0, 0.6);
      }
    }

    &__image {
      border-radius: 10px;
      width: 220px;
      height: 315px;

      @include tablet {
        height: 215px;
      }

      @include small-tablet {
        width: 190px;
      }

      @include small-duo {
        width: 150px;
        height: 160px;
      }

      @include mobile {
        height: 150px;
      }

      @include mobile-min {
        height: 135px;
      }

      @include mobile-small {
        width: 93px;
        height: 130px;
      }
    }

    &__post {
      padding-top: 20px;
      font-weight: 500;
      font-size: 25px;
      color: white;

      @include tablet {
        padding-top: 10px;
        font-weight: 500;
        font-size: 20px;
      }

      @include small-tablet {
        font-size: 15px;
      }

      @include small-duo {
        font-size: 13px;
      }

      @include mobile {
        padding-top: 5px;
        font-size: 10px;
      }

      @include mobile-min {
        padding-top: 3px;
      }
    }

    &__text {
      padding-top: 10px;
      font-weight: 400;
      font-size: 16px;
      color: #696161;

      @include tablet {
        padding-top: 7px;
        font-weight: 400;
        font-size: 13px;
      }

      @include small-tablet {
        font-size: 8px;
      }

      @include small-duo {
        font-size: 7px;
      }

      @include mobile {
        font-size: 7px;
      }
    }

    &__genre {
      padding-bottom: 20px;
      font-weight: 400;
      font-size: 16px;
      color: #696161;

      @include tablet {
        font-weight: 400;
        font-size: 13px;
      }

      @include small-tablet {
        font-size: 10px;
      }

      @include small-duo {
        font-size: 7px;
        padding-bottom: 10px;
      }

      @include mobile {
        font-size: 8px;
      }

      @include mobile-min {
        padding-bottom: 5px;
      }
    }
  }
}
