.header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;
  background: #524f4f4d;


  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header--fixed{
 position: fixed;
 left: 0;
 top: 0;
}



.logo {
  @include flex-all-sb;

  &__text {
    font-weight: 600;
    font-size: 29px;
    color: white;

    @include small-tablet {
      font-size: 22px;
    }

  }

}

.logo__image {
  @include small-tablet {
    width: 25px;
    height: 25px;
  }

  @include mobile {
    width: 20px;
    height: 20px;
  }

  @include mobile-min {
    width: 17px;
    height: 17px;
  }

}
