.programs-1 {
  position: relative;
  top: 0;
  left: 0;
  background-color: #000000e0;
  border-bottom: 1px solid #787373;

  &__card {

    &-poster {
      position: absolute;
      left: 0px;
      top: 0px;
      background: linear-gradient(to right, rgba(0, 0, 0, 0.8), transparent);
    }
  }


  &__imge {
    @include big-desktop {
      width: 1440px;
      height: 570px;
    }

    @include tablet {
      width: 1024px;
      height: 500px;
    }

    @include tablet-pro {
      width: 915px;
      height: 420px;
    }

    @include tablet-ipad {
      width: 820px;
      height: 400px;
    }

    @include small-tablet {
      width: 940px;
      height: 300px;
    }

    @include small-duo {
      width: 540px;
      height: 250px;
    }

    @include mobile {
      width: 425px;
      height: 200px;
    }

    @include mobile-min {
      width: 376px;
      height: 160px;
    }

    @include mobile-small {
      width: 320px;
      height: 140px;
    }
  }

  &__title {
    width: 600px;
    padding-bottom: 3px;
    font-weight: 500;
    font-size: 50px;
    color: white;

    @include big-desktop {
      font-weight: 500;
      font-size: 45px;
    }

    @include tablet {
      font-weight: 400;
    }

    @include small-tablet {
      font-size: 35px;
    }

    @include small-duo {
      font-size: 30px;
    }

    @include mobile {
      font-size: 25px;
    }

    @include mobile-min {
      font-size: 20px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 15px;
    color: #686868;

    @include mobile {
      font-size: 10px;
    }
  }

  &__header {
    padding-left: 125px;

    @include tablet-pro {
      padding-left: 100px;
    }

    @include tablet-ipad {
      padding-left: 75px;
    }

    @include small-tablet {
      padding-left: 50px;
    }

    @include mobile {
      padding-left: 20px;
    }
  }


  &__top {
    padding-top: 20px;
    padding-bottom: 15px;
    font-weight: 400;
    font-size: 40px;
    color: white;

    @include big-desktop {
      font-weight: 300;
      font-size: 35px;
    }

    @include tablet {
      font-weight: 200;
    }

    @include small-tablet {
      font-size: 25px;
    }

    @include small-duo {
      font-size: 20px;
    }
  }

  &__subtitle {
    padding-bottom: 30px;

    @include tablet {
      padding-bottom: 20px;
    }

    @include small-tablet {
      padding-bottom: 15px;
    }

    @include small-duo {
      padding-bottom: 10px;
    }

    @include mobile {
      padding-bottom: 5px;
    }

    &-hall {
      padding-bottom: 10px;
      font-weight: 400;
      font-size: 25px;
      color: white;

      @include tablet {
        font-weight: 300;
        font-size: 20px;
      }

      @include small-tablet {
        font-size: 15px;
      }

      @include small-duo {
        font-size: 13px;
      }
    }
  }

  &__movie {
    padding-bottom: 20px;
    @include flex-v-center;
  }

  &__data {
    padding-right: 10px;
    font-weight: 700;
    font-size: 20px;
    color: white;

    @include tablet {
      font-weight: 500;
    }

    @include small-tablet {
      font-weight: 400;
      font-size: 15px;
    }

    @include small-duo {
      font-weight: 300;
      font-size: 13px;
    }

    @include mobile-small {
      padding-right: 5px;
    }

  }

  &__bottom {
    font-weight: 400;
    font-size: 20px;
    color: white;

    @include tablet {
      font-weight: 300;
      ;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include small-duo {
      font-size: 13px;
    }

  }

  &__record {
    padding-top: 20px;
    padding-bottom: 20px;
    line-height: 35px;
    font-weight: 500;
    font-size: 25px;
    color: white;


    @include tablet {
      font-weight: 400;
      font-size: 20px;
    }

    @include small-tablet {
      font-weight: 300;
      font-size: 15px;
    }

    @include small-duo {
      font-size: 13px;
    }
  }

}

.programs-poster {
  position: absolute;
  padding-left: 125px;
  padding-top: 320px;
  width: 80%;
  height: 611px;

  @include big-desktop {
    height: 570px;
    padding-top: 350px;
  }

  @include tablet {
    height: 500px;
    padding-top: 370px;
  }

  @include tablet-pro {
    height: 420px;
    padding-top: 280px;
    padding-left: 100px;
  }

  @include tablet-ipad {
    height: 400px;
    padding-top: 250px;
    padding-left: 75px;
  }

  @include small-tablet {
    height: 300px;
    padding-top: 180px;
    padding-left: 50px;
  }

  @include small-duo {
    height: 250px;
    padding-top: 148px;
  }

  @include mobile {
    height: 200px;
    padding-top: 127px;
    padding-left: 20px;
  }

  @include mobile-min {
    height: 160px;
    padding-top: 112px;
  }

  @include mobile-small {
    height: 141px;
    padding-top: 105px;
  }
}
