.date-1 {
  position: relative;
  top: 0;
  left: 0;
  background-color: #000000e0;
  padding-bottom: 90px;
  border-bottom: 1px solid #787373;

  @include tablet-ipad {
    padding-bottom: 50px;
  }

  @include small-tablet {
    padding-bottom: 20px;
  }


  &__card {

    &-poster {
      position: absolute;
      top: 0px;
      padding-top: 360px;
      padding-right: 23px;
      background: linear-gradient(to right, rgba(0, 0, 0, 0.80), transparent);

      @include tablet-pro {
        padding-top: 285px;
      }

      @include small-tablet {
        padding-top: 186px;
      }

      @include small-duo {
        padding-top: 135px;
      }

      @include mobile-min {
        padding-top: 94px;
      }

      @include mobile-small {
        padding-top: 74px;
      }

    }
  }

  &__item-text {
    padding-left: 90px;
    width: 650px;
    height: 190px;

    @include big-desktop {
      padding-left: 80px;
      height: 210px;
    }

    @include tablet {
      height: 140px;
    }

    @include tablet-pro {
      height: 135px;
    }

    @include tablet-ipad {
      height: 115px;
    }

    @include tablet {
      padding-left: 70px;
    }

    @include small-tablet {
      padding-left: 60px;
    }

    @include small-duo {
      padding-left: 50px;
    }

    @include mobile {
      padding-left: 40px;
    }

    @include mobile-min {
      padding-left: 30px;
    }

    @include mobile-small {
      padding-left: 20px;
    }

    @include mobile {
      height: 66px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 50px;
    color: white;

    @include big-desktop {
      font-weight: 500;
      font-size: 45px;
    }

    @include tablet {
      font-weight: 400;
    }

    @include small-tablet {
      font-size: 35px;
    }

    @include small-duo {
      font-size: 30px;
    }

    @include mobile {
      font-size: 25px;
    }

    @include mobile-min {
      font-size: 20px;
    }

  }

  &__text {
    padding-top: 5px;
    font-weight: 400;
    font-size: 15px;
    color: #686868;

    @include mobile {
      font-size: 10px;
    }

    @include mobile-min {
      font-size: 7px;
    }
  }


  &__info {
    font-weight: 400;
    font-size: 25px;
    color: white;

    @include small-tablet {
      font-size: 20px;
    }

    &-time {
      padding-left: 105px;

      @include tablet-pro {
        padding-left: 70px;
      }

      @include tablet-ipad {
        padding-left: 5px;
      }
    }

    &-plot {
      display: flex;
      justify-content: space-evenly;
    }
  }

  &__blok {
    display: flex;
    justify-content: space-evenly;
  }

  &__body {
    display: flex;
    text-align: center;
    padding-top: 20px;

    @include small-duo {
      display: flex;
      flex-direction: column;
    }
  }

  &__description {
    width: 1000px;

    @include tablet {
      width: 707px;
    }

    @include tablet-pro {
      width: 600px;
    }

    @include small-tablet {
      width: 578px;
    }

    @include small-duo {
      width: 518px;
    }

    @include mobile {
      width: 397px;
    }

    @include mobile-12pro {
      width: 360px;
    }

    @include mobile-min {
      width: 352px;
    }

    @include mobile-galaxys {
      width: 341px;
    }

    @include mobile-small {
      width: 289px;
    }
  }

  &__number {
    font-weight: 500;
    font-size: 16px;
    color: white;

    @include small-tablet {
      font-size: 15px;
    }


  }

  &__subject {
    text-align: left;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: white;

    @include small-tablet {
      font-size: 15px;
    }
  }

  &__date {
    padding-top: 20px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    padding-left: 105px;

    @include tablet-pro {
      padding-left: 70px;
    }

    @include tablet-ipad {
      padding-left: 5px;
    }

    @include small-duo {
      width: 40%;
    }

    @include mobile {
      width: 50%;
    }



    &__hall {
      font-weight: 500;
      font-size: 18px;
      color: white;

      @include small-tablet {
        font-size: 16px;
      }
    }
  }

  &__youtube {
    width: 1000px;
    height: 500px;
    padding-top: 30px;

    @include tablet {
      width: 780px;
      height: 430px;
    }

    @include tablet-pro {
      width: 645px;
      height: 390px;
    }

    @include tablet-ipad {
      width: 575px;
      height: 355px;
    }

    @include small-duo {
      width: 488px;
    }

    @include mobile {
      width: 384px;
    }

    @include mobile-xr {
      width: 385px;
    }

    @include mobile-pixel {
      width: 293px;
      height: 400px;
    }
  }


  .btn__date {
    margin-top: 10px;
    background-color: #555555;
    border-radius: 20px;
    color: white;
    padding: 4px 4px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;

    &:hover {
      background-color: white;
      color: #555555;
    }

    @include small-tablet {
      font-size: 14px;
    }
  }

  .data-1__imge {
    width: 1520px;
    height: 550px;

    @include big-desktop {
      width: 1440px;
      height: 570px;
    }

    @include tablet {
      width: 1024px;
      height: 500px;
    }

    @include tablet-pro {
      width: 915px;
      height: 420px;
    }

    @include tablet-ipad {
      width: 820px;
      height: 400px;
    }

    @include small-tablet {
      width: 940px;
      height: 300px;
    }

    @include small-duo {
      width: 540px;
      height: 250px;
    }

    @include mobile {
      width: 425px;
      height: 200px;
    }

    @include mobile-min {
      width: 376px;
      height: 160px;
    }

    @include mobile-small {
      width: 320px;
      height: 140px;
    }
  }
}
