.btn {
  background-color: #555555;
  border-radius: 20px;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;

  @include tablet {
    padding: 10px 20px;
  }

  @include small-tablet {
    font-size: 12px;
    padding: 8px 15px;
  }

  @include small-duo {
    font-size: 11px;
    padding: 7px 14px;
  }

  @include mobile {
    font-size: 10px;
    padding: 7px 12px;
  }

  @include mobile-min {
    font-size: 8px;
    padding: 5px 10px;
  }
}
