.hero {
  position: relative;
  top: 0;
  left: 0;
  background-color: #000000e0;

  &__imge {
    width: 1520px;
    height: 610px;

    @include big-desktop {
      width: 1440px;
      height: 570px;
    }

    @include tablet {
      width: 1024px;
      height: 520px;
    }

    @include tablet-pro {
      width: 915px;
      height: 480px;
    }

    @include tablet-ipad {
      width: 820px;
      height: 400px;
    }

    @include small-tablet {
      width: 940px;
      height: 300px;
    }

    @include small-duo {
      width: 540px;
      height: 250px;
    }

    @include mobile {
      width: 425px;
      height: 200px;
    }

    @include mobile-min {
      width: 376px;
      height: 160px;
    }

    @include mobile-small {
      width: 320px;
      height: 140px;
    }
  }

  &__card {

    &-poster {
      position: absolute;
      bottom: 3px;
      padding-top: 601px;
      padding-right: 250px;
      background: linear-gradient(to right, rgba(0, 0, 0, 0.80), transparent);
    }
  }

  &__title {
    font-weight: 500;
    font-size: 60px;
    color: white;

    @include big-desktop {
      font-weight: 500;
      font-size: 45px;
    }

    @include tablet {
      font-weight: 400;
    }

    @include small-tablet {
      font-size: 35px;
    }

    @include small-duo {
      font-size: 30px;
    }

    @include mobile {
      font-size: 25px;
      width: 270px;
    }

    @include mobile-min {
      font-size: 20px;
    }
  }

  &__text {
    padding-top: 15px;
    font-weight: 400;
    font-size: 20px;
    width: 450px;
    color: #686868;

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      padding-top: 10px;
      font-size: 10px;
    }

    @include mobile-min {
      padding-top: 7px;
    }
  }

}

.poster-text {
  padding-top: 421px;
  padding-left: 85px;
  height: 305px;

  @include tablet {
    height: 220px;
  }

  @include small-tablet {
    height: 160px;
  }

  @include small-duo {
    height: 120px;
    width: 400px;
  }

  @include mobile {
    padding-left: 18px;
    height: 100px;
  }

  @include mobile-min {
    height: 65px;
  }

  @include mobile-small {
    height: 50px;
  }
}
