.footer {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: #000000e0;


  &__menu {
    @include flex-all-center;
  }

  &__text {
    padding-right: 400px;

    @include tablet {
      padding-right: 300px;
    }

    @include small-tablet {
      padding-right: 200px;
    }

    @include small-duo {
      padding-right: 100px;
    }

    @include mobile {
      padding-right: 50px;
    }
  }


  &__item {
    padding-bottom: 10px;

    @include small-duo {
      padding-bottom: 5px;
    }

    @include mobile-min {
      padding-bottom: 2px;
    }
  }

  &__link {
    font-weight: 300;
    font-size: 15px;
    color: white;

    @include small-duo {
      font-size: 13px;
    }

    @include mobile {
      font-size: 12px;
    }

    &:hover {
      color: #787373;
    }
  }

  &__title {
    text-align: center;
    padding-top: 20px;

    @include small-duo {
      padding-top: 13px;
    }

    @include mobile {
      padding-top: 10px;
    }

    @include mobile-min {
      padding-top: 5px;
    }
  }

  &__information {
    font-weight: 300;
    font-size: 15px;
    color: white;
    border-top: 1px solid #787373;

    @include small-duo {
      font-size: 12px;
    }

    @include mobile {
      font-size: 10px;
    }
  }
}

.social__link {
  display: flex;
  font-weight: 300;
  font-size: 15px;
  color: white;
  padding-left: 30px;
  padding-bottom: 10px;
  background-size: 20px;
  background-repeat: no-repeat;

  @include small-duo {
    font-size: 13px;
    background-size: 13px;
    padding-left: 17px;
  }

  @include mobile {
    font-size: 10px;
    background-size: 15px;
    padding-left: 19px;
  }

  @include mobile-min {
    background-size: 12px;
    padding-left: 16px;
  }

  &:hover {
    color: #787373;
  }

  &--in {
    background-image: url("../img/inst.svg");

  }

  &--tt {
    background-image: url("../img/tt.svg");
  }

  &--tl {
    background-image: url("../img/tl.svg");
  }

  &--yb {
    background-image: url("../img/yb.svg");
  }

  &--fb {
    background-image: url("../img/fb.svg");
  }

  &--tv {
    background-image: url("../img/tv.svg");
  }

  &--vk {
    background-image: url("../img/vk.svg");
  }
}
