.nav {
  @include flex-v-center;

  @include small-duo {
    position: fixed;
    left: 0px;
    top: 0px;
    overflow-y: auto;
    width: 100%;
    max-height: 55vh;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  @include mobile-small {
    top: 14px;
  }

  &__list {
    @include flex-v-center;


    @include small-duo {
      display: block;
      width: 100%;
      height: 100%;
      margin: 0px;
      padding: 30px 0px;
      list-style: none;
      text-align: center;
      background-color: rgba(69, 67, 67, 0.3);
      box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);
      flex-direction: column;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 50px;

      @include small-tablet {
        margin-right: 30px;
      }

      @include small-duo {
        margin-right: 0;
        margin-bottom: 10px;

      }
    }
  }

  &__link {
    font-weight: 600;
    font-size: 19px;
    color: white;

    @include small-tablet {
      font-size: 17px;
    }

    @include mobile {
      font-size: 15px;
    }
  }
}


.menu--active {
  opacity: 1;
  visibility: visible;
}
